/* Footer
------------------------------------*/
.footer-v6 .footer {
	padding: 70px 0;
	background: #161616;
}

.footer-v6 .footer p {
	color: #777;
}

/*Heading Fotoer*/
.footer-v6 .heading-footer {
	text-align: left;
    position: relative;
    margin-bottom: 30px;
    padding-bottom: 2px;
}

.footer-v6 .heading-footer:after {
    left: 0;
    bottom: 0;
    height: 1px;
    width: 50px;
    content: " ";
    background: #fff;
    position: absolute;
}

.footer-v6 .heading-footer h2 {
    color: #eee;
	font-size: 18px;
	text-align: left;
    text-transform: uppercase;
}

/*Link News*/
.footer-v6 .footer .link-news {
	margin-bottom: 0;
}

.footer-v6 .footer .link-news li {
	padding: 12px 0px;
	border-top: solid 1px #353535;
}

.footer-v6 .footer .link-news li:first-child {
	padding-top: 0;
	border-top: none;
}

.footer-v6 .footer .link-news li a {
	color: #777;
	font-size: 14px;
	margin-bottom: 3px;
	display: inline-block;
}

.footer-v6 .footer .link-news a:hover {
	color: #bbb;
}

.footer-v6 .footer .link-news li small {
	color: #555;
	display: block;
}

/*Link List*/
.footer-v6 .footer-link-list li {
	padding: 9px 0px;
	border-top: solid 1px #333;
}

.footer-v6 .footer-link-list li:first-child {
	padding-top: 0;
	border-top: none;
}

.footer-v6 .footer-link-list li a {
	color: #777;
	font-size: 14px;
	display: inline-block;
}

.footer-v6 .footer-link-list a:hover {
	color: #bbb;
}

/*Contacts*/
.footer-v6 .footer .contacts {
	margin-bottom: 0;
}

.footer-v6 .footer .contacts li {
	margin-bottom: 20px;
}

.footer-v6 .footer .contacts li:before,
.footer-v6 .footer .contacts li:after {
  	content: " ";
	display: table;
}

.footer-v6 .footer .contacts li:after {
	clear: both;
}

.footer-v6 .footer .contacts li i {
	float: left;
	width: 30px;
    height: 30px;
	color: #777;
	padding: 8px 5px;
	background: #333;
	text-align: center;
	margin: 4px 10px 0 0;
	display: inline-block;
}

.footer-v6 .footer .contacts li p {
	margin-bottom: 0;
}

.footer-v6 .footer .contacts li a {
	color: #777;
}

.footer-v6 .footer .contacts li a:hover {
	color: #bbb;
}

